import React from 'react'
import { Button, InputGroup, InputGroupAddon, InputGroupText, Input, FormGroup,  Label, Form, CustomInput, Container, Col, Row} from 'reactstrap';
import { getUserGeoData } from '../lib/get-userGeoData';
import Map from './Map'
import './Menu.css'

const options = {
  enableHighAccuracy: true,
  timeout: 5000,
  maximumAge: 0
};

class Menu extends React.Component {
  /* 
   * –í state —Ö—Ä–∞–Ω–∏–º –≥–µ–æ–¥–∞–Ω–Ω—ã–µ –¥–ª—è –ø–µ—Ä–µ–¥–∞—á–∏ –≤ –∫–∞—á–µ—Å—Ç–≤–µ –æ–¥–Ω–æ–≥–æ –∏–∑ –∞—Ä–≥—É–º–µ–Ω—Ç–æ–≤ 
   * –≤ –∑–∞–ø—Ä–æ—Å–µ –∫ API. –ß–µ—Ä–µ–∑ props –ø–µ—Ä–µ–¥–∞–µ–º –≥–µ–æ–¥–∞–Ω–Ω—ã–µ –≤ Map, –≥–¥–µ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç
   * –¥–∞–ª—å–Ω–µ–π—à–∞—è –≥–µ–Ω–µ—Ä–∞—Ü–∏—è http-–∑–∞–ø—Ä–æ—Å–∞ –∫ API –∏ –æ–±—Ä–∞–±–æ—Ç–∫–∞ –ø–æ–ª—É—á–µ–Ω–Ω–æ–≥–æ –æ—Ç–≤–µ—Ç–∞ –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞ –¥–ª—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ –Ω–∞ –∫–∞—Ä—Ç–µ.
   */
  constructor(props: Props) {
    super(props);
    this.state = {
      searchField : '',
      isToggleOn: false,
      inputLongitude: 0,
      inputLatitude: 0,
      geodata: {}
    };
    this.handleClick = this.handleClick.bind(this);
  };

  handleClick() {
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));
    console.log(this.state.isToggleOn)
  }

  handleChangeSearch (event) {
    const changedValue = event.target.value;
    console.log(changedValue);
    this.setState({searchField : changedValue});
    console.log(this.state);
  }

  handleChangeLatitude(event) {
    const changedValue = event.target.value;
    console.log(changedValue);
    this.setState({inputLatitude: changedValue});
    console.log(this.state);
  }

  handleChangeLongitude(event) {
    const changedValue = event.target.value;
    console.log(changedValue);
    this.setState({inputLongitude: changedValue});
    console.log(this.state);
  }

  goSearch = () => {
    console.log('GO!');
    getUserGeoData(options)
      .then(res => this.setState({geodata : res}))
      .catch(err =>  console.warn('ERROR(${err.code}): ${err.message}'));
    console.log(this.state['geodata']);
  }

  render() {
    const isToggleOn = this.state.isToggleOn;
    const latitude = this.state.geodata['latitude'];
    const longitude = this.state.geodata['longitude'];
    return (
    	<div>
    	  <div className='menu'>
        {/* –®–∞–ø–∫–∞ –ö–∞—Ä—Ç–æ—á–∫–∏ */}
          <h1>
            –ì–µ–æ–∫–æ–¥–µ—Ä
            <span role="img" aria-label="sheep">üêë</span>  
          </h1>

        {/* –ü–æ–¥–ª–æ–∂–∫–∞ */}
        <div className="outer-div">
          <div className="inner-div">

          {/* –≠–ª–µ–º–µ–Ω—Ç –ø–æ–∏—Å–∫–∞ */}
          <FormGroup>
            <Label for="addressValue" hidden>–ê–¥—Ä–µ—Å</Label>
            <Input 
              type="text"
              name="addressValue"
              id="addressValue"
              placeholder="–ê–¥—Ä–µ—Å"
              onChange={event => this.handleChangeSearch(event)}
            />
          </FormGroup>

          {/* –≠–ª–µ–º–µ–Ω—Ç –≤–≤–æ–¥–∞ –≥–µ–æ–¥–∞–Ω–Ω—ã—Ö */}
          {isToggleOn ?
            (<FormGroup>
              <Label for="latitudeValue" hidden>–®–∏—Ä–æ—Ç–∞</Label>
              <Input
                type="number"
                name="latitudeValue"
                id="latitudeValue"
                placeholder="–®–∏—Ä–æ—Ç–∞"
                onChange={event => this.handleChangeLatitude(event)}
              />
              <Label for="longitudeValue" hidden>–î–æ–ª–≥–æ—Ç–∞</Label>
              <Input
                type="number"
                name="password"
                id="longitudeValue"
                placeholder="–î–æ–ª–≥–æ—Ç–∞"
                onChange={event => this.handleChangeLongitude(event)}
              />
            </FormGroup>) 
            : ''
          }

          {/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –∫–æ–Ω—Ç—Ä–æ–ª—è —Ñ–æ—Ä–º—ã */}
          <Container>
            <Row>
              <Col sm={{ size: 0, order: 0}}>
                <Button onClick={this.goSearch}>–ü–æ–∏—Å–∫</Button>
              </Col>
              <Col sm={{ size: 6, order: 0}}>
                <CustomInput type="checkbox" id="exampleCustomCheckbox" onClick={this.handleClick} label="–≥–µ–æ—Ç–æ—á–∫–∞"/>
              </Col>
            </Row>
          </Container>

          {/* –≠–ª–µ–º–µ–Ω—Ç –≤—ã–≤–æ–¥–∞ –æ—Ç–≤–µ—Ç–∞ –æ—Ç –≥–µ–æ–∫–æ–¥–µ—Ä–∞ –∏ –∑–∞–¥–∞–Ω–Ω—ã—Ö –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç */}
          <div className="output-div">
            <p>{latitude}</p>
            <p>{longitude}</p>
          </div>

          </div>
        </div>
    	  </div>
        {/* –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –ö–∞—Ä—Ç–∞ */}
        <Map latitude={this.state.geodata['latitude']} longitude={this.state.geodata['longitude']} accuracy={this.state.geodata['accuracy']} />
    	</div>
    );
  }
}
